import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import { Link } from 'react-router-dom';
import './NavBar.css';
import MoreVertIcon from '@material-ui/icons/MoreVert';

import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';


const styles = {
  root: {
    flexGrow: 1
  },
  navBar: {
    backgroundColor: 'var(--lowlight)'
  },
  flex: {
    flex: 1
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20
  }
};

class NavBarMenu extends React.Component {
  state = {
    anchorEl: null,
  };

  handleClick = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorEl: null });
  };
 render() {
  const { anchorEl } = this.state;
  return (
    <div>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="title" color="inherit">
           <Link to="/home">
            <img href="/" src="/images/CDP_LOGO_BLUE.png" alt="logo" height="50" width="100" />
            </Link>
          </Typography>
          <div className="bigScreenNav" >
            <Button href="/learn" color="inherit">What is a Doula</Button>
            <Button href="/search" color="inherit">Search</Button>
            <Button href="/comingsoon" color="inherit">Register</Button>
          </div>
          <div className="smallScreenNav" >
            <MoreVertIcon />
            <Button href="/learn" color="inherit">What is a Doula</Button>
          </div>

        </Toolbar>
      </AppBar>
    </div>
    );
  }
}


export default withStyles(styles)(NavBarMenu);



